import React from 'react';
import {useForm} from 'react-hook-form';
import ReCAPTCHA from "react-google-recaptcha";


function Form({setData}) {

  const {register, handleSubmit, watch, formState: {errors}} = useForm();
  const onSubmit = data => setData(data);

  console.log(watch("exampleRequired"));

  return (
    <div className="md:w-1/2 px-8 md:px-0 m-auto">
    <form onSubmit={handleSubmit(onSubmit)}>
    <div className="grid grid-cols-4 gap-8">
        <div className="lg:col-span-2 col-span-4">
          {/* register your input into the hook by invoking the "register" function */}
          <input 
          className="w-full py-2 px-1 border-b border-gray-900 placeholder:text-gray-900 placeholder:font-thin outline-none" 
          placeholder="Name" 
          {...register("name", { required: true })} />
          {errors.name && <span className="text-xs">This field is required</span>}
        </div>
      {/* include validation with required or other standard HTML validation rules */}
        <div className="lg:col-span-2 col-span-4">  
          <input 
          className="w-full py-2 px-1 border-b border-gray-900 placeholder:text-gray-900 placeholder:font-thin outline-none"
          placeholder="Email"
          {...register("email", { required: true })} />
          {/* errors will return when field validation fails  */}
          {errors.email && <span className="text-xs">This field is required</span>}
        </div>
        <div className="col-span-4">
          <input
          className="w-full py-2 px-1 border-b border-gray-900 placeholder:text-gray-900 placeholder:font-thin outline-none"
          placeholder="Website url"
          {...register("weburl")}
          />
        </div>
        <div  className="col-span-4">
        <textarea 
          className="w-full py-2 px-1 h-40 border-b border-gray-900 placeholder:text-gray-900 placeholder:font-thin outline-none"   placeholder="How can I help you."
          {...register("message", {required: true})}></textarea>
          {errors.message && <span className="text-xs">This field is required</span>}
        </div>
        <div className="col-span-4">
        <ReCAPTCHA
          sitekey="6LewezMeAAAAAJV2mVUFvf25LEO5SpFqBt9T61AA"
          {...register("captcha")}
        />
        </div>
        {errors.captcha && <span className="text-xs">This field is required</span>}
        <div className="w-full col-span-4">
        <input 
          className="block mt-2 border px-2 py-1" 
          type="submit" />
        </div>  
    </div>
    </form>
    </div>
    
  )
}

export default Form