import {useEffect} from 'react';
import {gsap} from 'gsap';
import Logo from '../../yw-logo-horizontal.png';



function IntroductionContainer({logo, data}) {

   /** GSAP ANIMATION */
    useEffect(()=>{
    gsap.from('.logo',{duration:0.5, opacity: '0', delay: 0.2})
    gsap.from('.intro',{duration:0.5, opacity: '0', delay: 0.3, stagger: 1})
    gsap.to('.signature', {duration:1.0, opacity:1, stagger: 0.6})
  },[])


  return (
    <div className="flex justify-center">
      <div className="intro absolute md:bottom-10 bottom-10 left-5 md:w-96 w-56 ">
          <div className="signature font-windSong mb-32 text-yellow-800/20 text-[9vw] opacity-0 md:flex hidden">rdVajra</div> 
          <img className="logo w-52" src={Logo}   alt="yellow-webiste.com" />
          <div className="intro text-xs md:text-sm font-thin tracking-widest my-4 pl-1">
            <div dangerouslySetInnerHTML={{__html: data }} />
          </div>
      </div>  
    </div>
  )
}

export default IntroductionContainer
