import {useEffect} from 'react';
import {BrowserRouter, Routes, Route} from 'react-router-dom';
import Home from './pages/Home';
import Layout from './layout/Layout';
import About from './pages/About';
import Themes from './pages/Themes';
import Contact from './pages/Contact';
import Gallery from './pages/Gallery';
import Blogposts from './pages/Blogposts';
import WebResources from './pages/WebResources';
import Clients from './pages/Clients';


import {useDispatch, useSelector} from 'react-redux';


function App() {

  const dispatch = useDispatch();
  const count = useSelector(state => state.pageLoad.count);

  console.log(count);

  useEffect(()=>{
    //dispatch(actionLoadCount(count));
  },[dispatch,count])

  return (
    <BrowserRouter>
      <Layout>
          <Routes>
            <Route path="/" element={<Home />} />
            <Route path="/about" element={<About />} />
            <Route path="/gallery" element={<Gallery />} />
            <Route path="/theme-gallery/:id" element={<Themes />} />
            <Route path="/contact-me" element={<Contact />} />
            <Route path="/clients" element={<Clients />} />
            <Route path="/blog" element={<Blogposts />} />
            <Route path="/web-resources" element={<WebResources />} />
          </Routes>
      </Layout>
    </BrowserRouter>
    
  );
}

export default App;
